.btn {
    padding: 8px 20px;
    border-radius: 4px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}
.btn--secondary {
    background-color: var(--color-secondary);
    color: var(--color-white);
    border: 2px solid var(--color-secondary);
}
.btn--secondary:hover {
    background-color: var(--color-soft-secondary);
    border-color: var(--color-soft-secondary);
}
.btn--secondary:active {
    border-color: var(--color-primary);
}
.btn--primary {
    background-color: var(--color-primary);
    color: var(--color-white);
    border: 2px solid var(--color-primary);
}
.btn--primary:hover {
    background-color: var(--color-soft-primary);
    border-color: var(--color-soft-primary);
}
.btn--primary:active {
    border-color: var(--color-secondary);
}
.btn--outline {
    background-color: transparent;
    color: var(--color-primary);
    border: 1px solid var(--color-primary);
    transition: all 0.3s ease-out;
}
.btn--medium {
    padding: 8px 20px;
    font-size: 1.8rem;
}
.btn--large {
    padding: 12px 26px;
    font-size: 2rem;
}
.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 100%;
    text-decoration: none;
    font-size: 1.6rem;
    padding: 12px 20px;
}