.ProjectCard {
    padding: 12px 8px;
    width: min-content;
    scroll-snap-align: center;
}
.ProjectCard__container {
    position: relative;
    width: 240px;
    height: max-content;
    border-radius: 8px;
    background-color: var(--color-white);
    box-shadow: 0px 3px 9px var(--color-soft-gray);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: all .3s ease-in-out;
    outline: none;
}
.ProjectCard__container:hover,.ProjectCard__container:focus {
    transform: scale(1.04);
    box-shadow: 0px 6px 12px var(--color-soft-gray);
}
.ProjectCard__thumbnail {
    height: 140px;
    width: 100%;
    object-fit: cover;
    object-position: center;
}
.ProjectCard__body {
    padding: 12px 16px;
}
.ProjectCard__title {
    text-align: start;
    font-size: 2.0rem;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
    color: var(--color-soft-dark);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.ProjectCard__description {
    margin-top: 8px;
    height: 65px;
    font-size: 1.6rem;
    font-family: 'Lato', sans-serif;
    color: var(--color-soft-dark);
    overflow: hidden;
    white-space: wrap;
    text-overflow: ellipsis;
}
.ProjectCard__footer {
    padding: 12px 16px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    align-items: center;
}
.ProjectCard__repo-link {
    font-weight: 500;
    font-size: 1.6rem;
    font-family: 'Lato', sans-serif;
    color: var(--color-soft-secondary);
}
.ProjectCard__repo-link:hover,.ProjectCard__repo-link:focus {
    color: var(--color-secondary);
}